body {
  font-family: Arial, Helvetica, sans-serif;
  margin: 20px;
}
h1 {
  font-size: 40px;
  font-weight: bold;
  margin-left: 20px;
}
button {
  border-style: none;
  border: rgb(80, 80, 80) solid 1px;
  border-radius: 10px;
  background-color: rgb(199, 199, 199);
  color: black;
  cursor: pointer;
  padding: 10px;
  font-size: 20px;
  font-weight: bold;
  box-shadow: 0px 0px 10px rgb(77, 77, 77);
  margin-left: 20px;
}
button:hover {
  background-color: rgb(77, 77, 77);
  color: white;
}
input {
  border-style: none;
  border: rgb(80, 80, 80) solid 1px;
  border-radius: 10px;
  background-color: rgb(214, 214, 214);
  color: black;
  padding: 10px;
  font-size: 20px;
  font-weight: bold;
  box-shadow: 0px 0px 10px rgb(77, 77, 77);
  margin-left: 20px;
}
ul {
  list-style-type: none;
  overflow: hidden;
  background-color: rgb(199, 199, 199);
  box-shadow: 0px 0px 10px rgb(77, 77, 77);
}

li {
  font-size: 20px;
  color: black;
}
